@import '../variables';

.modal {
    &__background {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.7);
        z-index: 99;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__content {
        background-color: #fff;
        padding: 16px 32px;
        border-radius: 3px;
        text-align: center;
    }

    &__text {
        font-size: 1.5em;
    }

    &__button {
        background: $blue;
        text-transform: uppercase;
        width: 300px;
        height: 50px;
        font-size: 16px;
        padding: 8px;
        cursor: pointer;
        color: $yellow;
        font-family: 'Roboto', sans-serif;
        outline: none;
        transition: transform .2s;
        border-radius: 16px;
    }
}

.modal--visible {
    display: flex;
    opacity: 1;
    transition: opacity .2s;
}

.modal--hidden {
    display: none;
    opacity: 0;
}